<template>
  <div class="bg">
      <div class="search rowTop">
            <el-input placeholder="" ></el-input>

            <el-input placeholder="" v-if="article"></el-input>
           <el-select v-model="value" placeholder="" v-else-if="doctor">
                <el-option
                v-for="item in options" 
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="commentValue" placeholder="" v-else-if="comment">
                <el-option
                v-for="item in commentOption" 
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
           
          
                <el-select v-model="goodsValue" placeholder="" v-else-if="goodsList">
                <el-option
                v-for="item in goods" 
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="goodsValue1" placeholder="" v-else-if="goodsList">
                <el-option
                v-for="item in goods1" 
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
          
           
            
          
         <el-button type="primary" icon="el-icon-search" size="mini"></el-button>
      </div>
  </div>
</template>

<script>
   import {mapGetters} from 'vuex';

    export default {
        data() {
            return {
               options: [{
                value: '1',
                label: ''
                }, {
                value: '2',
                label: ''
                }, {
                value: '3',
                label: ''
                }, {
                value: '4',
                label: ''
                }],
                commentOption:[{ value: '1',
                label: ''
                }, {
                value: '2',
                label: ''
                }, {
                value: '3',
                label: ''
                }, {
                value: '4',
                label: ''}],
                commentValue: '',
                goods:[{ value: '1',
                    label: ''
                    }, {
                    value: '2',
                    label: ''
                    }, {
                    value: '3',
                    label: ''
                    }, {
                    value: '4',
                label: ''}],
                  goods1:[{ value: '1',
                    label: '1'
                    }, {
                    value: '2',
                    label: '2'
                    }, {
                    value: '3',
                    label: '3'
                    }, {
                    value: '4',
                label: '4'}],
                goodsValue1:'',
                goodsValue2:''
            }
        },
         props: {
            doctor:{
                type:String,
                required:false
            },
            article:{
                type:String,
                required:false
            },
            comment:{
                type:String,
                required:false
            },
            goodsList:{
                type:String,
                required:false
            },
             user:{
                type:String,
                required:false
            },
        },

     methods:{
         confirm(){}
     },
    watch:{
        doctor:{
            handler:function (newValue, oldValue) {
            console.log(oldValue,"......"+newValue);
            },
            deep: true,
          immediate: true
        },
        comment:{
            handler:function (newValue, oldValue) {
            console.log(oldValue,"......"+newValue);
            },
            deep: true,
          immediate: true
        },

    },
     created(){//
      var _this=this;
     },
      computed:{//
        ...mapGetters({
            
        }),
      },
     }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.search {padding:1rem 0.5rem 0.5rem;}
.el-input,.el-select {max-width: 15%;margin-right: 0.5rem;}

</style>



